<script lang="ts">
  import { onMount } from 'svelte'
  import { GridLayout, Panel } from '$lib/components/ui'
  import { connectWigle, disconnectWigle, isConnected, deviceCount, connectionError } from '$lib/stores/websocket/wigle'
  import { DeviceList, MapView, TAKConfigForm, FileManager, FilterManager, Notifications } from './components'
  import type { WifiDevice } from '$lib/services/api/types'

  let selectedDevice = $state<WifiDevice | null>(null)
  let activePanel = $state<'devices' | 'map' | 'tak' | 'files' | 'filters'>('devices')

  onMount(() => {
    // Connect to WebSocket for real-time updates
    connectWigle()
    
    // Listen for device selection from map
    window.addEventListener('device-selected', handleDeviceSelection)
    
    return () => {
      disconnectWigle()
      window.removeEventListener('device-selected', handleDeviceSelection)
    }
  })

  function handleDeviceSelection(event: Event) {
    const customEvent = event as CustomEvent<WifiDevice>
    selectedDevice = customEvent.detail
    activePanel = 'devices'
  }

  function handleDeviceSelect(device: WifiDevice) {
    selectedDevice = device
    if (device.latitude && device.longitude) {
      activePanel = 'map'
    }
  }

  function handleImportComplete(result: { imported: number; skipped: number }) {
    window.wigleNotifications?.success(
      `Successfully imported ${result.imported} devices (${result.skipped} skipped)`
    )
  }
</script>

<main class="wigle-app">
  <Notifications />
  
  <header class="app-header">
    <h1>WigleToTAK</h1>
    <div class="header-info">
      <span class="device-count"> {$deviceCount} devices</span>
      {#if $isConnected}
        <span class="connection-status connected"> Connected</span>
      {:else if $connectionError}
        <span class="connection-status error" title={$connectionError.message}> Error</span>
      {:else}
        <span class="connection-status disconnected"> Disconnected</span>
      {/if}
    </div>
  </header>

  <nav class="tab-navigation">
    <button 
      class="tab-button" 
      class:active={activePanel === 'devices'}
      onclick={() => activePanel = 'devices'}
    >
      Devices
    </button>
    <button 
      class="tab-button" 
      class:active={activePanel === 'map'}
      onclick={() => activePanel = 'map'}
    >
      Map
    </button>
    <button 
      class="tab-button" 
      class:active={activePanel === 'tak'}
      onclick={() => activePanel = 'tak'}
    >
      TAK Config
    </button>
    <button 
      class="tab-button" 
      class:active={activePanel === 'filters'}
      onclick={() => activePanel = 'filters'}
    >
      Filters
    </button>
    <button 
      class="tab-button" 
      class:active={activePanel === 'files'}
      onclick={() => activePanel = 'files'}
    >
      Import/Export
    </button>
  </nav>

  <div class="app-content">
    {#if activePanel === 'devices'}
      <GridLayout cols={1}>
        <Panel title="WiFi Devices" resizable>
          <DeviceList onDeviceSelect={handleDeviceSelect} />
        </Panel>
      </GridLayout>
    {:else if activePanel === 'map'}
      <GridLayout cols={1}>
        <Panel title="Device Map" resizable>
          <MapView {selectedDevice} />
        </Panel>
      </GridLayout>
    {:else if activePanel === 'tak'}
      <GridLayout cols={1}>
        <Panel title="TAK Server Configuration">
          <TAKConfigForm />
        </Panel>
      </GridLayout>
    {:else if activePanel === 'filters'}
      <GridLayout cols={1}>
        <Panel title="Scan Filters">
          <FilterManager />
        </Panel>
      </GridLayout>
    {:else if activePanel === 'files'}
      <GridLayout cols={1}>
        <Panel title="File Management">
          <FileManager onImportComplete={handleImportComplete} />
        </Panel>
      </GridLayout>
    {/if}
  </div>
</main>

