<script lang="ts">
  import { onMount } from 'svelte'
  import { Card } from '$lib/components/ui'
  import { WigleApiClient } from '$lib/services/api/WigleApiClient'
  import { deviceList, isConnected } from '$lib/stores/websocket/wigle'
  import type { WifiDevice } from '$lib/services/api/types'
  import type { DeviceFilter } from '$lib/services/api/WigleApiClient'

  interface Props {
    onDeviceSelect?: (device: WifiDevice) => void
  }

  let { onDeviceSelect }: Props = $props()

  let searchQuery = $state('')
  let filterType = $state<'AP' | 'Client' | 'All'>('All')
  let filterSeen = $state<'active' | 'recent' | 'all'>('all')
  let minSignal = $state(-100)
  let loading = $state(false)
  let apiClient: WigleApiClient

  // Filtered devices based on search and filters
  const filteredDevices = $derived.by(() => {
    let devices = $deviceList

    // Apply search filter
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      devices = devices.filter(device => 
        device.mac.toLowerCase().includes(query) ||
        device.ssid?.toLowerCase().includes(query) ||
        device.manufacturer?.toLowerCase().includes(query)
      )
    }

    // Apply type filter
    if (filterType !== 'All') {
      devices = devices.filter(device => device.type === filterType)
    }

    // Apply signal filter
    devices = devices.filter(device => device.signal >= minSignal)

    // Apply seen filter
    const now = Date.now()
    const fiveMinutes = 5 * 60 * 1000
    const oneHour = 60 * 60 * 1000
    
    if (filterSeen === 'active') {
      devices = devices.filter(device => now - device.lastSeen < fiveMinutes)
    } else if (filterSeen === 'recent') {
      devices = devices.filter(device => now - device.lastSeen < oneHour)
    }

    // Sort by signal strength
    return devices.sort((a, b) => b.signal - a.signal)
  })

  onMount(() => {
    apiClient = new WigleApiClient()
    if (!$isConnected) {
      loadDevices()
    }
  })

  async function loadDevices() {
    loading = true
    try {
      const filter: DeviceFilter = {
        type: filterType,
        minSignal,
        seen: filterSeen
      }
      
      const response = await apiClient.getDevices(filter)
      if (response.data) {
        // Update device list from API if WebSocket is not connected
        // This would need to be implemented in the store
        console.log('Loaded devices:', response.data)
      }
    } catch (error) {
      console.error('Failed to load devices:', error)
    } finally {
      loading = false
    }
  }

  function formatSignal(signal: number): string {
    return `${signal} dBm`
  }

  function formatTime(timestamp: number): string {
    const date = new Date(timestamp)
    const now = Date.now()
    const diff = now - timestamp

    if (diff < 60000) {
      return 'Just now'
    } else if (diff < 3600000) {
      const minutes = Math.floor(diff / 60000)
      return `${minutes}m ago`
    } else if (diff < 86400000) {
      const hours = Math.floor(diff / 3600000)
      return `${hours}h ago`
    } else {
      return date.toLocaleString()
    }
  }

  function getSignalClass(signal: number): string {
    if (signal >= -50) return 'signal-excellent'
    if (signal >= -60) return 'signal-good'
    if (signal >= -70) return 'signal-fair'
    if (signal >= -80) return 'signal-weak'
    return 'signal-poor'
  }

  function getDeviceIcon(device: WifiDevice): string {
    if (device.type === 'AP') {
      return ''
    } else {
      return ''
    }
  }
</script>

<div class="device-list">
  <Card class="filter-card">
    <div class="filters">
      <div class="search-bar">
        <input
          type="text"
          bind:value={searchQuery}
          placeholder="Search by MAC, SSID, or manufacturer..."
          class="search-input"
        />
      </div>
      
      <div class="filter-controls">
        <div class="filter-group">
          <label>Type:</label>
          <select bind:value={filterType} class="filter-select">
            <option value="All">All</option>
            <option value="AP">Access Points</option>
            <option value="Client">Clients</option>
          </select>
        </div>
        
        <div class="filter-group">
          <label>Seen:</label>
          <select bind:value={filterSeen} class="filter-select">
            <option value="all">All</option>
            <option value="recent">Recent (1h)</option>
            <option value="active">Active (5m)</option>
          </select>
        </div>
        
        <div class="filter-group">
          <label>Min Signal:</label>
          <input
            type="range"
            bind:value={minSignal}
            min="-100"
            max="-30"
            class="signal-slider"
          />
          <span class="signal-value">{formatSignal(minSignal)}</span>
        </div>
      </div>
    </div>
  </Card>

  <div class="device-grid">
    {#if loading}
      <Card class="loading-card">
        <p>Loading devices...</p>
      </Card>
    {:else if filteredDevices.length === 0}
      <Card class="empty-card">
        <p>No devices found matching filters</p>
      </Card>
    {:else}
      {#each filteredDevices as device (device.mac)}
        <Card 
          class="device-card"
          onclick={() => onDeviceSelect?.(device)}
        >
          <div class="device-header">
            <span class="device-icon">{getDeviceIcon(device)}</span>
            <h3 class="device-ssid">{device.ssid || 'Hidden'}</h3>
            <span class="device-type">{device.type}</span>
          </div>
          
          <div class="device-info">
            <div class="info-row">
              <span class="label">MAC:</span>
              <span class="value mac">{device.mac}</span>
            </div>
            
            {#if device.manufacturer}
              <div class="info-row">
                <span class="label">Vendor:</span>
                <span class="value">{device.manufacturer}</span>
              </div>
            {/if}
            
            <div class="info-row">
              <span class="label">Signal:</span>
              <span class="value signal {getSignalClass(device.signal)}">
                {formatSignal(device.signal)}
              </span>
            </div>
            
            <div class="info-row">
              <span class="label">Channel:</span>
              <span class="value">{device.channel} ({device.frequency} MHz)</span>
            </div>
            
            {#if device.latitude && device.longitude}
              <div class="info-row">
                <span class="label">Location:</span>
                <span class="value"> {device.latitude.toFixed(6)}, {device.longitude.toFixed(6)}</span>
              </div>
            {/if}
            
            <div class="info-row">
              <span class="label">Last seen:</span>
              <span class="value">{formatTime(device.lastSeen)}</span>
            </div>
          </div>
        </Card>
      {/each}
    {/if}
  </div>
  
  <div class="device-stats">
    <span>Total: {filteredDevices.length} devices</span>
    {#if $isConnected}
      <span class="connection-status connected"> Live updates</span>
    {:else}
      <span class="connection-status disconnected"> Offline</span>
    {/if}
  </div>
</div>

