<script lang="ts">
  import { Card, Button } from '$lib/components/ui'
  import { WigleApiClient } from '$lib/services/api/WigleApiClient'

  interface Props {
    onImportComplete?: (result: { imported: number; skipped: number }) => void
  }

  let { onImportComplete }: Props = $props()

  let apiClient = new WigleApiClient()
  let fileInput: HTMLInputElement
  let selectedFile = $state<File | null>(null)
  let importing = $state(false)
  let exporting = $state(false)
  let error = $state<string | null>(null)
  let success = $state<string | null>(null)
  let importProgress = $state(0)

  // Export options
  let exportFormat = $state<'csv' | 'json' | 'kml'>('csv')
  let takExportFormat = $state<'xml' | 'json'>('xml')

  function handleFileSelect(event: Event) {
    const input = event.target as HTMLInputElement
    const file = input.files?.[0]
    
    if (file) {
      // Validate file type
      if (!file.name.endsWith('.csv') && !file.name.endsWith('.wiglecsv')) {
        error = 'Please select a valid WigleCSV file'
        selectedFile = null
        return
      }
      
      selectedFile = file
      error = null
    }
  }

  async function importFile() {
    if (!selectedFile) {
      error = 'Please select a file to import'
      return
    }

    importing = true
    error = null
    success = null
    importProgress = 0

    try {
      // Simulate progress updates (real implementation would use actual progress)
      const progressInterval = setInterval(() => {
        importProgress = Math.min(importProgress + 10, 90)
      }, 200)

      const response = await apiClient.importWigleCSV(selectedFile)
      
      clearInterval(progressInterval)
      importProgress = 100

      if (response.data) {
        const { imported, skipped, errors } = response.data
        
        if (errors.length > 0) {
          error = `Import completed with errors: ${errors.join(', ')}`
        } else {
          success = `Successfully imported ${imported} devices (${skipped} skipped)`
        }
        
        onImportComplete?.({ imported, skipped })
        
        // Reset file selection
        selectedFile = null
        if (fileInput) fileInput.value = ''
      }
    } catch (err) {
      error = 'Failed to import file'
      console.error(err)
    } finally {
      importing = false
      importProgress = 0
    }
  }

  async function exportDevices() {
    exporting = true
    error = null
    success = null

    try {
      const response = await apiClient.exportDevices(exportFormat)
      
      if (response.data) {
        // Create download link
        const blob = response.data
        const url = URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = `wigle_devices_${new Date().toISOString()}.${exportFormat}`
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
        
        success = `Devices exported as ${exportFormat.toUpperCase()}`
      }
    } catch (err) {
      error = 'Failed to export devices'
      console.error(err)
    } finally {
      exporting = false
    }
  }

  async function exportTAKMessages() {
    exporting = true
    error = null
    success = null

    try {
      const response = await apiClient.exportTAKMessages(takExportFormat)
      
      if (response.data) {
        // Create download link
        const blob = response.data
        const url = URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = `tak_messages_${new Date().toISOString()}.${takExportFormat}`
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
        
        success = `TAK messages exported as ${takExportFormat.toUpperCase()}`
      }
    } catch (err) {
      error = 'Failed to export TAK messages'
      console.error(err)
    } finally {
      exporting = false
    }
  }

  function formatFileSize(bytes: number): string {
    if (bytes < 1024) return bytes + ' B'
    if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(1) + ' KB'
    return (bytes / (1024 * 1024)).toFixed(1) + ' MB'
  }

  // Clear messages after 5 seconds
  $effect(() => {
    if (success) {
      const timer = setTimeout(() => success = null, 5000)
      return () => clearTimeout(timer)
    }
  })

  $effect(() => {
    if (error) {
      const timer = setTimeout(() => error = null, 5000)
      return () => clearTimeout(timer)
    }
  })
</script>

<div class="file-manager">
  <Card class="import-section">
    <h3>Import WigleCSV</h3>
    
    <div class="file-input-wrapper">
      <input
        type="file"
        accept=".csv,.wiglecsv"
        onchange={handleFileSelect}
        bind:this={fileInput}
        class="file-input"
        id="wigle-file-input"
      />
      <label for="wigle-file-input" class="file-label">
        <span class="file-icon"></span>
        <span class="file-text">
          {selectedFile ? selectedFile.name : 'Choose WigleCSV file...'}
        </span>
      </label>
    </div>
    
    {#if selectedFile}
      <div class="file-info">
        <span class="file-name">{selectedFile.name}</span>
        <span class="file-size">{formatFileSize(selectedFile.size)}</span>
      </div>
    {/if}
    
    {#if importing && importProgress > 0}
      <div class="progress-bar">
        <div 
          class="progress-fill" 
          style="width: {importProgress}%"
        ></div>
      </div>
    {/if}
    
    <Button
      variant="primary"
      onclick={importFile}
      disabled={!selectedFile || importing}
      class="import-btn"
    >
      {importing ? `Importing... ${importProgress}%` : 'Import File'}
    </Button>
    
    <div class="help-text">
      Import WiFi scan data from Kismet's WigleCSV export format.
      The file should contain MAC addresses, SSIDs, signal strength, and GPS coordinates.
    </div>
  </Card>
  
  <Card class="export-section">
    <h3>Export Data</h3>
    
    <div class="export-group">
      <h4>Export Devices</h4>
      <div class="export-options">
        <select bind:value={exportFormat} class="format-select">
          <option value="csv">CSV Format</option>
          <option value="json">JSON Format</option>
          <option value="kml">KML (Google Earth)</option>
        </select>
        <Button
          variant="secondary"
          onclick={exportDevices}
          disabled={exporting}
        >
          {exporting ? 'Exporting...' : 'Export Devices'}
        </Button>
      </div>
    </div>
    
    <div class="export-group">
      <h4>Export TAK Messages</h4>
      <div class="export-options">
        <select bind:value={takExportFormat} class="format-select">
          <option value="xml">XML Format</option>
          <option value="json">JSON Format</option>
        </select>
        <Button
          variant="secondary"
          onclick={exportTAKMessages}
          disabled={exporting}
        >
          {exporting ? 'Exporting...' : 'Export TAK Messages'}
        </Button>
      </div>
    </div>
  </Card>
  
  {#if error}
    <div class="message error">{error}</div>
  {/if}
  
  {#if success}
    <div class="message success">{success}</div>
  {/if}
</div>

