<script lang="ts">
  import { onMount } from 'svelte'
  import { Card, Button } from '$lib/components/ui'
  import { WigleApiClient } from '$lib/services/api/WigleApiClient'
  import type { WigleConfig } from '$lib/services/api/types'
  import type { TAKServerStatus } from '$lib/services/api/WigleApiClient'

  interface Props {
    onConfigSaved?: (config: WigleConfig) => void
  }

  let { onConfigSaved }: Props = $props()

  let apiClient: WigleApiClient
  let loading = $state(false)
  let saving = $state(false)
  let testing = $state(false)
  let error = $state<string | null>(null)
  let success = $state<string | null>(null)
  let serverStatus = $state<TAKServerStatus | null>(null)

  // Form fields
  let config = $state<WigleConfig>({
    takServer: '',
    takPort: 6969,
    callsign: '',
    team: 'Yellow',
    role: 'Team Member',
    antennaHeight: 1.5,
    scanInterval: 30
  })

  // Predefined options
  const teams = ['White', 'Yellow', 'Orange', 'Magenta', 'Red', 'Maroon', 'Purple', 
                 'Dark Blue', 'Blue', 'Cyan', 'Teal', 'Green', 'Dark Green', 'Brown']
  
  const roles = ['Team Member', 'Team Lead', 'HQ', 'Sniper', 'Medic', 'Forward Observer',
                 'RTO', 'K9', 'Airsoft', 'Sticky', 'JTAC', 'Engineer', 'Recon', 'Security']

  onMount(async () => {
    apiClient = new WigleApiClient()
    await loadConfig()
    await checkServerStatus()
  })

  async function loadConfig() {
    loading = true
    error = null
    
    try {
      const response = await apiClient.getTAKConfig()
      if (response.data) {
        config = response.data
      }
    } catch (err) {
      error = 'Failed to load TAK configuration'
      console.error(err)
    } finally {
      loading = false
    }
  }

  async function saveConfig() {
    saving = true
    error = null
    success = null
    
    try {
      const response = await apiClient.updateTAKConfig(config)
      if (response.data) {
        config = response.data
        success = 'Configuration saved successfully'
        onConfigSaved?.(response.data)
        
        // Check server status after saving
        await checkServerStatus()
      }
    } catch (err) {
      error = 'Failed to save configuration'
      console.error(err)
    } finally {
      saving = false
    }
  }

  async function checkServerStatus() {
    try {
      const response = await apiClient.getTAKStatus()
      if (response.data) {
        serverStatus = response.data
      }
    } catch (err) {
      console.error('Failed to check server status:', err)
    }
  }

  async function testConnection() {
    testing = true
    error = null
    success = null
    
    try {
      // Save config first
      await saveConfig()
      
      // Try to connect
      const connectResponse = await apiClient.connectTAK()
      if (connectResponse.data?.connected) {
        success = 'Successfully connected to TAK server'
        await checkServerStatus()
      } else {
        error = 'Failed to connect to TAK server'
      }
    } catch (err) {
      error = 'Connection test failed'
      console.error(err)
    } finally {
      testing = false
    }
  }

  async function disconnect() {
    try {
      await apiClient.disconnectTAK()
      await checkServerStatus()
      success = 'Disconnected from TAK server'
    } catch (err) {
      error = 'Failed to disconnect'
      console.error(err)
    }
  }

  function validateForm(): boolean {
    if (!config.takServer) {
      error = 'TAK server address is required'
      return false
    }
    
    if (!config.callsign) {
      error = 'Callsign is required'
      return false
    }
    
    if (config.takPort < 1 || config.takPort > 65535) {
      error = 'Port must be between 1 and 65535'
      return false
    }
    
    return true
  }

  // Clear messages after 5 seconds
  $effect(() => {
    if (success) {
      const timer = setTimeout(() => success = null, 5000)
      return () => clearTimeout(timer)
    }
  })

  $effect(() => {
    if (error) {
      const timer = setTimeout(() => error = null, 5000)
      return () => clearTimeout(timer)
    }
  })
</script>

<Card class="tak-config-form">
  <h3>TAK Server Configuration</h3>
  
  {#if loading}
    <div class="loading">Loading configuration...</div>
  {:else}
    <form onsubmit={(e) => { e.preventDefault(); saveConfig() }}>
      <div class="form-section">
        <h4>Connection Settings</h4>
        
        <div class="form-group">
          <label for="server">TAK Server Address</label>
          <input
            id="server"
            type="text"
            bind:value={config.takServer}
            placeholder="192.168.1.100 or tak.example.com"
            required
          />
        </div>
        
        <div class="form-group">
          <label for="port">Port</label>
          <input
            id="port"
            type="number"
            bind:value={config.takPort}
            min="1"
            max="65535"
            required
          />
        </div>
      </div>
      
      <div class="form-section">
        <h4>Identity Settings</h4>
        
        <div class="form-group">
          <label for="callsign">Callsign</label>
          <input
            id="callsign"
            type="text"
            bind:value={config.callsign}
            placeholder="STINKY-1"
            required
          />
        </div>
        
        <div class="form-group">
          <label for="team">Team Color</label>
          <select id="team" bind:value={config.team}>
            {#each teams as team}
              <option value={team}>{team}</option>
            {/each}
          </select>
        </div>
        
        <div class="form-group">
          <label for="role">Role</label>
          <select id="role" bind:value={config.role}>
            {#each roles as role}
              <option value={role}>{role}</option>
            {/each}
          </select>
        </div>
      </div>
      
      <div class="form-section">
        <h4>Sensor Settings</h4>
        
        <div class="form-group">
          <label for="antenna">Antenna Height (meters)</label>
          <input
            id="antenna"
            type="number"
            bind:value={config.antennaHeight}
            min="0"
            max="100"
            step="0.1"
          />
          <span class="help-text">Height of antenna above ground</span>
        </div>
        
        <div class="form-group">
          <label for="interval">Scan Interval (seconds)</label>
          <input
            id="interval"
            type="number"
            bind:value={config.scanInterval}
            min="5"
            max="300"
          />
          <span class="help-text">How often to send updates</span>
        </div>
      </div>
      
      {#if serverStatus}
        <div class="server-status" class:connected={serverStatus.connected}>
          <h4>Server Status</h4>
          <div class="status-info">
            <div class="status-row">
              <span>Connection:</span>
              <span class="status-value">
                {serverStatus.connected ? ' Connected' : ' Disconnected'}
              </span>
            </div>
            {#if serverStatus.connected}
              <div class="status-row">
                <span>Server:</span>
                <span class="status-value">{serverStatus.server}:{serverStatus.port}</span>
              </div>
              <div class="status-row">
                <span>Messages sent:</span>
                <span class="status-value">{serverStatus.messagesSent}</span>
              </div>
              {#if serverStatus.errors > 0}
                <div class="status-row">
                  <span>Errors:</span>
                  <span class="status-value error">{serverStatus.errors}</span>
                </div>
              {/if}
            {/if}
          </div>
        </div>
      {/if}
      
      {#if error}
        <div class="message error">{error}</div>
      {/if}
      
      {#if success}
        <div class="message success">{success}</div>
      {/if}
      
      <div class="form-actions">
        <Button
          type="submit"
          variant="primary"
          disabled={saving || testing}
        >
          {saving ? 'Saving...' : 'Save Configuration'}
        </Button>
        
        <Button
          type="button"
          variant="secondary"
          onclick={testConnection}
          disabled={saving || testing || !validateForm()}
        >
          {testing ? 'Testing...' : 'Test Connection'}
        </Button>
        
        {#if serverStatus?.connected}
          <Button
            type="button"
            variant="danger"
            onclick={disconnect}
          >
            Disconnect
          </Button>
        {/if}
      </div>
    </form>
  {/if}
</Card>

