  }

  h4 {
    margin-bottom: 1rem;
    color: var(--text-secondary);
    font-size: 1rem;
  }

  .loading {
    text-align: center;
    padding: 2rem;
    color: var(--text-secondary);
  }

  form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  .form-section {
    padding: 1rem;
    background: var(--bg-secondary);
    border-radius: 0.5rem;
  }

  .form-group {
    margin-bottom: 1rem;
  }

  .form-group:last-child {
    margin-bottom: 0;
  }

  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: var(--text-primary);
  }

  input,
  select {
    width: 100%;
    padding: 0.5rem 0.75rem;
    border: 1px solid var(--border-color);
    border-radius: 0.25rem;
    background: var(--bg-primary);
    color: var(--text-primary);
    font-size: 1rem;
  }

  input:focus,
  select:focus {
    outline: none;
    border-color: var(--primary);
  }

  .help-text {
    display: block;
    margin-top: 0.25rem;
    font-size: 0.875rem;
