<script lang="ts">
  import { onMount } from 'svelte'
  import { Card, Button } from '$lib/components/ui'
  import { WigleApiClient } from '$lib/services/api/WigleApiClient'
  import type { ScanSettings } from '$lib/services/api/WigleApiClient'

  interface Props {
    onSettingsChanged?: (settings: ScanSettings) => void
  }

  let { onSettingsChanged }: Props = $props()

  let apiClient: WigleApiClient
  let loading = $state(false)
  let saving = $state(false)
  let error = $state<string | null>(null)
  let success = $state<string | null>(null)

  // Filter settings
  let settings = $state<ScanSettings>({
    scanInterval: 30,
    signalThreshold: -90,
    maxAge: 3600,
    channels: [],
    ignoreBSSIDs: []
  })

  // UI state
  let newBSSID = $state('')
  let selectedChannels = $state(new Set<number>())
  let channelInput = $state('')

  // Common WiFi channels
  const commonChannels = {
    '2.4GHz': [1, 6, 11],
    '5GHz': [36, 40, 44, 48, 52, 56, 60, 64, 100, 104, 108, 112, 116, 120, 124, 128, 132, 136, 140, 144, 149, 153, 157, 161, 165]
  }

  onMount(async () => {
    apiClient = new WigleApiClient()
    await loadSettings()
  })

  async function loadSettings() {
    loading = true
    error = null

    try {
      const response = await apiClient.getScanSettings()
      if (response.data) {
        settings = response.data
        selectedChannels = new Set(settings.channels)
      }
    } catch (err) {
      error = 'Failed to load scan settings'
      console.error(err)
    } finally {
      loading = false
    }
  }

  async function saveSettings() {
    saving = true
    error = null
    success = null

    // Update channels from selection
    settings.channels = Array.from(selectedChannels).sort((a, b) => a - b)

    try {
      const response = await apiClient.updateScanSettings(settings)
      if (response.data) {
        settings = response.data
        success = 'Settings saved successfully'
        onSettingsChanged?.(response.data)
      }
    } catch (err) {
      error = 'Failed to save settings'
      console.error(err)
    } finally {
      saving = false
    }
  }

  function addBSSID() {
    if (!newBSSID) return

    // Validate MAC address format
    const macRegex = /^([0-9A-Fa-f]{2}[:-]){5}([0-9A-Fa-f]{2})$/
    if (!macRegex.test(newBSSID)) {
      error = 'Invalid MAC address format (use XX:XX:XX:XX:XX:XX)'
      return
    }

    const normalized = newBSSID.toUpperCase()
    if (!settings.ignoreBSSIDs.includes(normalized)) {
      settings.ignoreBSSIDs = [...settings.ignoreBSSIDs, normalized]
      newBSSID = ''
      error = null
    }
  }

  function removeBSSID(bssid: string) {
    settings.ignoreBSSIDs = settings.ignoreBSSIDs.filter(b => b !== bssid)
  }

  function toggleChannel(channel: number) {
    if (selectedChannels.has(channel)) {
      selectedChannels.delete(channel)
    } else {
      selectedChannels.add(channel)
    }
    selectedChannels = new Set(selectedChannels)
  }

  function selectAllChannels(band: '2.4GHz' | '5GHz') {
    commonChannels[band].forEach(ch => selectedChannels.add(ch))
    selectedChannels = new Set(selectedChannels)
  }

  function clearAllChannels() {
    selectedChannels.clear()
    selectedChannels = new Set(selectedChannels)
  }

  function addCustomChannel() {
    const channel = parseInt(channelInput)
    if (!isNaN(channel) && channel >= 1 && channel <= 165) {
      selectedChannels.add(channel)
      selectedChannels = new Set(selectedChannels)
      channelInput = ''
    } else {
      error = 'Invalid channel number (1-165)'
    }
  }

  function formatAge(seconds: number): string {
    if (seconds < 60) return `${seconds} seconds`
    if (seconds < 3600) return `${Math.floor(seconds / 60)} minutes`
    if (seconds < 86400) return `${Math.floor(seconds / 3600)} hours`
    return `${Math.floor(seconds / 86400)} days`
  }

  // Clear messages after 5 seconds
  $effect(() => {
    if (success) {
      const timer = setTimeout(() => success = null, 5000)
      return () => clearTimeout(timer)
    }
  })

  $effect(() => {
    if (error) {
      const timer = setTimeout(() => error = null, 5000)
      return () => clearTimeout(timer)
    }
  })
</script>

<div class="filter-manager">
  <Card>
    <h3>Scan Filter Settings</h3>
    
    {#if loading}
      <div class="loading">Loading settings...</div>
    {:else}
      <form onsubmit={(e) => { e.preventDefault(); saveSettings() }}>
        <div class="settings-section">
          <h4>Detection Settings</h4>
          
          <div class="setting-group">
            <label for="interval">Scan Interval</label>
            <div class="slider-group">
              <input
                id="interval"
                type="range"
                bind:value={settings.scanInterval}
                min="5"
                max="300"
                step="5"
              />
              <span class="slider-value">{settings.scanInterval}s</span>
            </div>
            <span class="help-text">How often to perform WiFi scans</span>
          </div>
          
          <div class="setting-group">
            <label for="threshold">Signal Threshold</label>
            <div class="slider-group">
              <input
                id="threshold"
                type="range"
                bind:value={settings.signalThreshold}
                min="-100"
                max="-30"
              />
              <span class="slider-value">{settings.signalThreshold} dBm</span>
            </div>
            <span class="help-text">Minimum signal strength to track devices</span>
          </div>
          
          <div class="setting-group">
            <label for="maxage">Device Timeout</label>
            <div class="slider-group">
              <input
                id="maxage"
                type="range"
                bind:value={settings.maxAge}
                min="60"
                max="7200"
                step="60"
              />
              <span class="slider-value">{formatAge(settings.maxAge)}</span>
            </div>
            <span class="help-text">Remove devices not seen for this duration</span>
          </div>
        </div>
        
        <div class="settings-section">
          <h4>Channel Selection</h4>
          
          <div class="channel-presets">
            <Button
              type="button"
              variant="secondary"
              size="small"
              onclick={() => selectAllChannels('2.4GHz')}
            >
              Select 2.4GHz
            </Button>
            <Button
              type="button"
              variant="secondary"
              size="small"
              onclick={() => selectAllChannels('5GHz')}
            >
              Select 5GHz
            </Button>
            <Button
              type="button"
              variant="secondary"
              size="small"
              onclick={clearAllChannels}
            >
              Clear All
            </Button>
          </div>
          
          <div class="channel-grid">
            <div class="channel-band">
              <h5>2.4GHz Channels</h5>
              <div class="channel-list">
                {#each commonChannels['2.4GHz'] as channel}
                  <label class="channel-checkbox">
                    <input
                      type="checkbox"
                      checked={selectedChannels.has(channel)}
                      onchange={() => toggleChannel(channel)}
                    />
                    <span>{channel}</span>
                  </label>
                {/each}
              </div>
            </div>
            
            <div class="channel-band">
              <h5>5GHz Channels</h5>
              <div class="channel-list">
                {#each commonChannels['5GHz'] as channel}
                  <label class="channel-checkbox">
                    <input
                      type="checkbox"
                      checked={selectedChannels.has(channel)}
                      onchange={() => toggleChannel(channel)}
                    />
                    <span>{channel}</span>
                  </label>
                {/each}
              </div>
            </div>
          </div>
          
          <div class="custom-channel">
            <input
              type="number"
              bind:value={channelInput}
              placeholder="Custom channel"
              min="1"
              max="165"
              class="channel-input"
            />
            <Button
              type="button"
              variant="secondary"
              size="small"
              onclick={addCustomChannel}
            >
              Add Channel
            </Button>
          </div>
          
          <div class="selected-channels">
            Selected: {selectedChannels.size === 0 ? 'All channels' : 
                     Array.from(selectedChannels).sort((a, b) => a - b).join(', ')}
          </div>
        </div>
        
        <div class="settings-section">
          <h4>Ignored BSSIDs</h4>
          
          <div class="bssid-input">
            <input
              type="text"
              bind:value={newBSSID}
              placeholder="AA:BB:CC:DD:EE:FF"
              class="bssid-field"
            />
            <Button
              type="button"
              variant="secondary"
              size="small"
              onclick={addBSSID}
            >
              Add BSSID
            </Button>
          </div>
          
          <div class="bssid-list">
            {#if settings.ignoreBSSIDs.length === 0}
              <div class="empty-list">No BSSIDs ignored</div>
            {:else}
              {#each settings.ignoreBSSIDs as bssid}
                <div class="bssid-item">
                  <span class="bssid-mac">{bssid}</span>
                  <button
                    type="button"
                    class="remove-btn"
                    onclick={() => removeBSSID(bssid)}
                  >
                    
                  </button>
                </div>
              {/each}
            {/if}
          </div>
          
          <span class="help-text">
            Add MAC addresses of devices to ignore (e.g., your own access points)
          </span>
        </div>
        
        {#if error}
          <div class="message error">{error}</div>
        {/if}
        
        {#if success}
          <div class="message success">{success}</div>
        {/if}
        
        <div class="form-actions">
          <Button
            type="submit"
            variant="primary"
            disabled={saving}
          >
            {saving ? 'Saving...' : 'Save Settings'}
          </Button>
          
          <Button
            type="button"
            variant="secondary"
            onclick={loadSettings}
          >
            Reset
          </Button>
        </div>
      </form>
    {/if}
  </Card>
</div>

