<script lang="ts">
  import { onMount, onDestroy } from 'svelte'
  import { Card } from '$lib/components/ui'
  import { deviceList } from '$lib/stores/websocket/wigle'
  import type { WifiDevice } from '$lib/services/api/types'

  interface Props {
    selectedDevice?: WifiDevice | null
    center?: { lat: number; lon: number }
    zoom?: number
  }

  let { 
    selectedDevice = null,
    center = { lat: 40.7128, lon: -74.0060 }, // Default to NYC
    zoom = 13
  }: Props = $props()

  let mapContainer: HTMLDivElement
  let leafletMap: any
  let markersLayer: any
  let deviceMarkers = new Map<string, any>()

  // Device type colors
  const markerColors = {
    AP: '#22c55e',      // Green for Access Points
    Client: '#3b82f6',  // Blue for Clients
    Unknown: '#6b7280'  // Gray for Unknown
  }

  onMount(async () => {
    // Dynamically import Leaflet to avoid SSR issues
    const L = await import('leaflet')
    await import('leaflet/dist/leaflet.css')

    // Initialize map
    leafletMap = L.map(mapContainer).setView([center.lat, center.lon], zoom)

    // Add tile layer
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: ' OpenStreetMap contributors',
      maxZoom: 19
    }).addTo(leafletMap)

    // Create markers layer group
    markersLayer = L.layerGroup().addTo(leafletMap)

    // Add devices to map
    updateDeviceMarkers()

    // Watch for device updates
    const unsubscribe = deviceList.subscribe(() => updateDeviceMarkers())

    return () => {
      unsubscribe()
    }
  })

  onDestroy(() => {
    if (leafletMap) {
      leafletMap.remove()
    }
  })

  async function updateDeviceMarkers() {
    if (!leafletMap || !markersLayer) return

    const L = await import('leaflet')
    const devices = $deviceList.filter(d => d.latitude && d.longitude)

    // Remove markers for devices that no longer exist
    deviceMarkers.forEach((marker, mac) => {
      if (!devices.find(d => d.mac === mac)) {
        markersLayer.removeLayer(marker)
        deviceMarkers.delete(mac)
      }
    })

    // Add or update markers for current devices
    devices.forEach(device => {
      if (!device.latitude || !device.longitude) return

      const existingMarker = deviceMarkers.get(device.mac)
      
      if (existingMarker) {
        // Update existing marker position
        existingMarker.setLatLng([device.latitude, device.longitude])
        existingMarker.setPopupContent(createPopupContent(device))
      } else {
        // Create new marker
        const icon = createDeviceIcon(device, L)
        const marker = L.marker([device.latitude, device.longitude], { icon })
          .bindPopup(createPopupContent(device))
          .addTo(markersLayer)

        // Add click handler
        marker.on('click', () => {
          // Emit device selection event
          window.dispatchEvent(new CustomEvent('device-selected', { detail: device }))
        })

        deviceMarkers.set(device.mac, marker)
      }
    })

    // Update selected device highlight
    if (selectedDevice && selectedDevice.latitude && selectedDevice.longitude) {
      const marker = deviceMarkers.get(selectedDevice.mac)
      if (marker) {
        marker.openPopup()
        leafletMap.setView([selectedDevice.latitude, selectedDevice.longitude], 16)
      }
    }
  }

  function createDeviceIcon(device: WifiDevice, L: any) {
    const color = markerColors[device.type as keyof typeof markerColors] || markerColors.Unknown
    const size = device.type === 'AP' ? 30 : 24
    
    // Create signal strength indicator
    const signalStrength = Math.max(0, Math.min(100, (device.signal + 100) * 2))
    
    return L.divIcon({
      className: 'device-marker',
      html: `
        <div class="marker-container" style="background-color: ${color};">
          <div class="signal-indicator" style="width: ${signalStrength}%;"></div>
          <span class="device-type-icon">${device.type === 'AP' ? '' : ''}</span>
        </div>
      `,
      iconSize: [size, size],
      iconAnchor: [size / 2, size / 2]
    })
  }

  function createPopupContent(device: WifiDevice): string {
    const now = Date.now()
    const timeDiff = now - device.lastSeen
    const lastSeenText = timeDiff < 60000 ? 'Just now' : 
                        timeDiff < 3600000 ? `${Math.floor(timeDiff / 60000)}m ago` :
                        `${Math.floor(timeDiff / 3600000)}h ago`

    return `
      <div class="device-popup">
        <h4>${device.ssid || 'Hidden Network'}</h4>
        <div class="popup-info">
          <div><strong>MAC:</strong> ${device.mac}</div>
          <div><strong>Type:</strong> ${device.type}</div>
          <div><strong>Signal:</strong> ${device.signal} dBm</div>
          <div><strong>Channel:</strong> ${device.channel}</div>
          ${device.manufacturer ? `<div><strong>Vendor:</strong> ${device.manufacturer}</div>` : ''}
          <div><strong>Last seen:</strong> ${lastSeenText}</div>
          ${device.latitude && device.longitude ? 
            `<div><strong>Location:</strong> ${device.latitude.toFixed(6)}, ${device.longitude.toFixed(6)}</div>` : ''}
        </div>
      </div>
    `
  }

  // Effect to update selected device
  $effect(() => {
    if (selectedDevice && deviceMarkers.has(selectedDevice.mac)) {
      updateDeviceMarkers()
    }
  })

  // Map controls
  function zoomIn() {
    leafletMap?.zoomIn()
  }

  function zoomOut() {
    leafletMap?.zoomOut()
  }

  function centerOnDevices() {
    if (!leafletMap || !markersLayer) return

    const bounds = markersLayer.getBounds()
    if (bounds.isValid()) {
      leafletMap.fitBounds(bounds, { padding: [50, 50] })
    }
  }

  function toggleHeatmap() {
    // TODO: Implement heatmap overlay
    console.log('Heatmap toggle not yet implemented')
  }
</script>

<Card class="map-view">
  <div class="map-header">
    <h3>Device Map</h3>
    <div class="map-controls">
      <button class="control-btn" onclick={zoomIn} title="Zoom in">
        <span>+</span>
      </button>
      <button class="control-btn" onclick={zoomOut} title="Zoom out">
        <span></span>
      </button>
      <button class="control-btn" onclick={centerOnDevices} title="Fit all devices">
        <span></span>
      </button>
      <button class="control-btn" onclick={toggleHeatmap} title="Toggle heatmap">
        <span></span>
      </button>
    </div>
  </div>
  
  <div class="map-container" bind:this={mapContainer}></div>
  
  <div class="map-legend">
    <div class="legend-item">
      <span class="legend-marker ap"></span>
      <span>Access Points</span>
    </div>
    <div class="legend-item">
      <span class="legend-marker client"></span>
      <span>Clients</span>
    </div>
    <div class="legend-stats">
      {$deviceList.filter(d => d.latitude && d.longitude).length} devices on map
    </div>
  </div>
</Card>

