<script lang="ts">
  import type { HTMLAttributes } from 'svelte/elements';
  
  interface PanelProps extends HTMLAttributes<HTMLDivElement> {
    title?: string;
    collapsible?: boolean;
    defaultExpanded?: boolean;
    class?: string;
  }
  
  let {
    title = '',
    collapsible = false,
    defaultExpanded = true,
    class: className = '',
    children,
    ...restProps
  }: PanelProps = $props();
  
  let expanded = $state(defaultExpanded);
  
  function toggleExpanded() {
    if (collapsible) {
      expanded = !expanded;
    }
  }
</script>

<div
  class="bg-surface-100 dark:bg-surface-800 rounded-lg overflow-hidden {className}"
  {...restProps}
>
  {#if title}
    <div
      class="px-6 py-4 border-b border-surface-200 dark:border-surface-700 flex items-center justify-between"
      class:cursor-pointer={collapsible}
      onclick={toggleExpanded}
      role={collapsible ? "button" : undefined}
      tabindex={collapsible ? 0 : undefined}
      onkeydown={(e) => {
        if (collapsible && (e.key === 'Enter' || e.key === ' ')) {
          e.preventDefault();
          toggleExpanded();
        }
      }}
    >
      <h3 class="text-lg font-semibold text-surface-900 dark:text-surface-100">
        {title}
      </h3>
      {#if collapsible}
        <svg
          class="w-5 h-5 text-surface-500 transition-transform duration-200"
          class:rotate-180={!expanded}
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M19 9l-7 7-7-7"
          />
        </svg>
      {/if}
    </div>
  {/if}
  
  {#if expanded}
    <div class="p-6 animate-fade-in">
      {@render children?.()}
    </div>
  {/if}
</div>